import React from 'react'

const Card = ({data}) => {
  return (
    <>

    
      {data.map((curtElem)=>{
        return(
            <>
                <div className='row mb-4'>
        <div className='col-sm-3'>
            <img className='w-100' src={curtElem.imgsrc} />
        </div>
        <div className='col-sm-7'>
            <p className='theme-color DocSpecialist'>{curtElem.category}</p>
            <h2 className='DocName'>{curtElem.name}</h2>
            <p className='DocExpnc'>{curtElem.experience}</p>
            <h3 className='DocCenter'>{curtElem.center}</h3>
        </div>
        <div className='col-sm-2'>
            <div className='h-100 position-relative'>
                <h1 className='theme-color priceH'>{curtElem.price}</h1>
                <button className='btn btn-primary btn-theme FindRsltBTN bottom-0 w-100'>Details</button>
            </div>
        </div>
    </div>
            </>
        )
      })}

        <nav aria-label="Page navigation example" className='mt-5'>
        <ul class="pagination justify-content-center">
            <li class="page-item">
            <a class="page-link" href="#" aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
            </a>
            </li>
            <li class="page-item"><a class="page-link" href="#">1</a></li>
            <li class="page-item"><a class="page-link" href="#">2</a></li>
            <li class="page-item"><a class="page-link" href="#">3</a></li>
            <li class="page-item">
            <a class="page-link" href="#" aria-label="Next">
                <span aria-hidden="true">&raquo;</span>
            </a>
            </li>
        </ul>
        </nav>
    </>
  )
}

export default Card
