import React from 'react'
import { Link } from 'react-router-dom'

const Footer = () => {
  return (
    <>
    <div className='container'>
        <div className='row footer'>
            <div className='col-md-3'>
                <img src='../images/FooterLogo.png' alt='footerLogo' />
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore.</p>
            </div>
            <div className='col-md-9 col-12'>
                <div className='row'>
                    <div className='col-md-3'>
                        <h5 className='FooterHeading'>Service</h5>
                        <ul className='footerList'>
                            <li><Link to='/find-a-doctor'>Find Doctors</Link></li>
                            <li><Link to=''>Consultation</Link></li>
                            <li><Link to=''>Book an Appointement</Link></li>
                            <li><Link to=''>Medicine Store</Link></li>
                        </ul>
                    </div>
                    <div className='col-md-3'>
                        <h5 className='FooterHeading'>Resource</h5>
                        <ul className='footerList'>
                            <li><Link to='/doctor-profile'>Our Doctors</Link></li>
                            <li><Link to='/store'>Store</Link></li>
                            <li><Link to='/blog'>Blog</Link></li>
                            <li><Link to='/faq'>FAQ</Link></li>
                            <li><Link to='/about'>About Us</Link></li>
                            <li><Link to='/contact'>Contact Us</Link></li>
                        </ul>
                    </div>
                    <div className='col-md-3'>
                        <h5 className='FooterHeading'>Our Social Media</h5>
                        <ul className='footerList'>
                            <li><Link to=''>Medium</Link></li>
                            <li><Link to=''>Instagram</Link></li>
                            <li><Link to=''>Facebook</Link></li>
                            <li><Link to=''>Twitter</Link></li>
                            <li><Link to=''>Youtube</Link></li>
                        </ul>
                    </div>
                    <div className='col-md-3'>
                        <h5 className='FooterHeading'>Our Social Media</h5>
                        <form>
                            <label className='mt-3'>Your Email</label>
                            <input type="text" className='form-control mt-3' />
                            <button className='btn btn-danger btn-theme mt-3'>Subscribe</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
    <div className='footerBtm'>
        <div className='d-flex footerBtmInner justify-content-center'>
            <div className='d-flex'>
                <img src='../images/Footer_location_on.png' alt="Footer_location_on" />
                <div>8819 Ohio St. South Gate, California 90280</div>
            </div>
            <div className='d-flex'>
                <img src='../images/email.png' alt="email" />
                <div>hello@meetdoctor.com</div>
            </div>
            <div className='d-flex'>
                <img src='../images/call.png' alt="call" />
                <div>+271 386-647-3637</div>
            </div>
        </div>
    </div>
    </>
  )
}

export default Footer
