import React from 'react'
import { Link } from 'react-router-dom'

const Specialist = () => {
  return (
    <>
      <div className='container Mt-100banner'>
        <div className='d-flex my-5 justify-content-between align-items-center'>
            <h2>Specialist</h2>
            <Link to='/'>See All</Link>
        </div>
        <div className='row SpecialistCards'>
        <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Anesthesiology.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Anesthesiology</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>
            <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Dermatology.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Dermatology</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>
            <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Radiology.png' alt='Radiology' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Radiology</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>            
        </div>
      </div>
      {/* end of container */}
    </>
  )
}

export default Specialist
