import React from 'react'
import { Link } from 'react-router-dom'
const Treatment = () => {
  return (
    <>
    <div className='container'>
      <div className='d-flex my-5 justify-content-between align-items-center'>
            <h2>Treatment</h2>
            <Link to='/'>See All</Link>
        </div>
        <div className='row mb-5 TreatmentCards'>
            <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='card'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Anesthesiology.png' alt='Anesthesiology' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Anesthesiology</h4>
                        </div>                                               
                    </div>
                    <ul className='treatmentLisr'>
                            <li>Critical Care Medicine</li>
                            <li>Hospice and palliative care </li>
                            <li>Pain Medicine</li>
                            <li>Pediatic Anesthesiology</li>
                            <li>Sleep Medicine</li>
                        </ul>  
                    </div>                   
                </div>
            </div>
            <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='card'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/EmergencyMedicine.png' alt='EmergencyMedicine' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Emergency Medicine</h4>
                        </div>                                               
                    </div>
                    <ul className='treatmentLisr'>
                            <li>Emergency medical services</li>
                            <li>Internal medicine / Critical care </li>
                            <li>Medicine</li>
                            <li>Medical toxicology</li>
                            <li>Pain medicine</li>
                        </ul>  
                    </div>                   
                </div>
            </div>
            <div className='col-md-4'>
                <div className='position-relative'>
                    <div className='card'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Family-Medicine.png' alt='Family-Medicine' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Family Medicine</h4>
                        </div>                                               
                    </div>
                        <ul className='treatmentLisr'>
                            <li>Adolescent medicine</li>
                            <li>Geriatric medicine</li>
                            <li>Hospice and palliative medicine</li>
                            <li>Pain medicine</li>
                            <li>Sports medicine</li>
                        </ul>  
                    </div>                   
                </div>
            </div>           
        </div>
        </div>
    </>
  )
}

export default Treatment
