import React from 'react'
import { NavLink } from 'react-router-dom'
import $ from 'jquery';
import "jquery-ui-dist/jquery-ui";
import {useEffect} from "react";

const SideMenu = () => {
    useEffect(() => {
        $('.toggleMenuIcon').click(function(){
            $('.SideMenuOuter').addClass('showSideMenu');
        });
        $('.CloseIconMobileMenu').click(function(){
            $('.SideMenuOuter').removeClass('showSideMenu');
        })
      }, []);

  return (
    <>
     <div className='SideMenuOuter'>
        <div className='SideTop d-flex justify-content-between'>
            <div className=''>
                <img src='../images/logo.png' alt='logo' />
            </div>
            <div className=''>
                <img src='../images/close-menu-icon.png' alt='close-menu-icon' className='CloseIconMobileMenu' />
            </div>
        </div>        
        {/* end of SideTop */}
        <div className='MobileMenuItems'>
            <ul>
            <li class="nav-item">
                        <NavLink className="nav-link" to='/'>Home</NavLink>                        
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/find-a-doctor'>Find Doctor</NavLink>
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/store'>Store</NavLink>
                      </li>
                      <li class="nav-item">
                      <NavLink className="nav-link" to='/blog'>Blog</NavLink>
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/faq'>FAQ</NavLink>

                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/about'>About</NavLink>

                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/contact'>Contact</NavLink>

                      </li>
            </ul>
        </div>
        {/* end of MobileMenuItems */}
     </div>
      {/*end of SideMenuOuter  */}
    </>
  )
}

export default SideMenu
