import React from 'react'

const Card = ({Data}) => {
  return (
    <>
    <div className='row TreatmentCards'>
      {Data.map((CurtElem)=>{
        // const [name,imgsrc, Tablets,price] = CurtElem;
        return(
            
            <>
            <div className='col-md-4 col-6 mb-4'>
                <div className="card">
                    <img src={CurtElem.imgsrc} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">{CurtElem.name}</h5>
                        <p className="card-text">{CurtElem.Tablets}</p>
                        <h5 className="card-title Price">{CurtElem.price}</h5>
                        <a href="#" className="btn btn-primary btn-theme w-100">Add to Cart</a>
                    </div>
                </div>
            </div>
            </>
        )
      })}
      </div>
    </>
  )
}

export default Card
