import React from 'react'
import { NavLink } from 'react-router-dom'


const Navbar = () => {
  return (
    <>
     <div className='header'>
      
            <div className='container'>
              <nav class="navbar navbar-expand-lg">
              <img className='toggleMenuIcon' src='../images/toggleMenuIcon.png' alt='toggleMenuIcon' />
                <div class="container-fluid topHdrMb">
                  <a class="navbar-brand" href="#"><img src='../images/logo.png' alt="logo" /></a>
                  
                  <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                  </button>
                  <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav me-auto mb-2 mb-lg-0 mainmenu">
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/'>Home</NavLink>                        
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/find-a-doctor'>Find Doctor</NavLink>
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/store'>Store</NavLink>
                      </li>
                      <li class="nav-item">
                      <NavLink className="nav-link" to='/blog'>Blog</NavLink>
                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/faq'>FAQ</NavLink>

                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/about'>About</NavLink>

                      </li>
                      <li class="nav-item">
                        <NavLink className="nav-link" to='/contact'>Contact</NavLink>

                      </li>

                    </ul>
                    <form class="d-flex" role="search">
                      {/* <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search"> */}
                      <button class="btn btn-theme" type="submit">Get Started</button>
                    </form>
                  </div>
                </div>
              </nav>
            </div>
          </div> 
    </>
  )
}

export default Navbar
