import React from 'react'
import GeneralFaq from './GeneralFaq'
import AppoinmentFaq from './AppoinmentFaq'
import PaymentFaq from './PaymentFaq'
import ConsultationFaq from './ConsultationFaq'
import GetInTouch from '../HomePage/GetInTouch'

const FAQContent = () => {
  return (
    <>
    <div className='container containerFAQ'>
        <div className='row justify-content-center my-5'>
            <div class="col-md-7 col-9">
                <div class="input-group mb-3" style={{border:"1px solid #c7c7c7", borderRadius:"7px"}}>
                    <span class="input-group-text">
                        <img src="../images/search.png" height="15px" alt="search" />
                    </span>
                    <input type="text" class="form-control border-left-0 border-right-0" aria-label="" placeholder="Search" />
                    
                </div>
            </div>
            <div className='col-md-2 col-3'>
                <button className='btn btn-primary btn-theme'>Search</button>
            </div>
        </div>
        <GeneralFaq />
        <AppoinmentFaq />
        <PaymentFaq />
        <ConsultationFaq />

        <div className='row justify-content-center mt-4 mb-5'>
                <button className='btn btn-theme' style={{width:"130px"}}>Learn More</button>
           
        </div>
        
      </div>
      <GetInTouch />
    </>
  )
}

export default FAQContent
