import React from 'react'
import { Link } from 'react-router-dom'

const StoreContent = () => {
  return (
    <>
      <div className='container MedicineProductCategory-mt-80'>
        <div className='d-flex my-5 justify-content-between align-items-center'>
            <h2>Medicine Product Category</h2>
            <Link to='/'>See All</Link>
        </div>
        <div className='row mb-5 zIn-1'>
        <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Overlay.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Child and Baby Care</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>
            <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Ear-Care.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Ear Care</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>
            <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Eye-care.png' alt='Eye-care' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Eye Care</h4>
                        </div>                        
                    </div>                    
                </div>
            </div> 
            <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Overlay.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Vit and Supplement</h4>
                        </div>                        
                    </div>                    
                </div>
            </div> 
            <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/HealthCare.png' alt='Overlay' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Health Care</h4>
                        </div>                        
                    </div>                    
                </div>
            </div> 
            <div className='col-md-4  mt-4'>
                <div className='position-relative'>
                    <div className='specialistImg'>
                        <img className='w-100' src='../images/Oral-Care.png' alt='Oral-Care' />
                        <div className='specialistName'>
                            <h4 className='text-center'>Oral Care</h4>
                        </div>                        
                    </div>                    
                </div>
            </div>            
        </div>
      </div>
      {/* end of container */}
    </>
  )
}

export default StoreContent
