import React from 'react'

const ArticleDetail = () => {
  return (
    <>
      <div className='HeroImage'>
          <img className='w-100 position-relative' src='../images/Background.png' />
          <div className='bannerContainer'>
            <div className='container'>
              <div className='ArticleBannerImage'>
                <img src='../images/articleBanerImg.png' alt='articleBanerImg' />
              </div>
            </div>  
          </div>
        </div> 

    </>
  )
}

export default ArticleDetail
